export default {
  methods: {
    updateValue () {
      this.$emit('input', this.value)
      this.$emit('on-change', this.value)
    }
  },
  model: {
    prop: 'modelValue'
  },
  props: {
    modelValue: {
      required: true
    },
    value: {
      required: true
    }
  }
}



